/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

/* cssrest */
body {
  font-family: 'Roboto', 'Noto Sans', sans-serif; }
  body h2 {
    line-height: 1.5;
    font-size: 48px;
    padding-top: 60px;
    padding-bottom: 40px;
    color: #650300;
    text-align: center; }
  body h3 {
    font-size: 48px;
    line-height: 72px;
    color: #AA0601;
    font-weight: bold;
    font-style: italic; }

.header {
  max-width: 100%;
  background-color: #AA0601;
  display: flex;
  justify-content: space-between; }
  .header .header-logo {
    background: url("https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/week3/logo.png") no-repeat;
    width: 80px;
    height: 49px;
    margin: 30px;
    text-indent: 101%;
    overflow: hidden;
    background-size: cover; }
  .header .header-logo:hover {
    opacity: 0.8; }
  .header .header-nav {
    display: flex;
    align-items: center;
    font-size: 20px; }
    .header .header-nav .header-nav-li a {
      text-decoration: none;
      color: #ffffff;
      padding-right: 20px;
      padding-left: 20px; }
    .header .header-nav .header-nav-li a:hover {
      opacity: 0.8; }

@media (max-width: 992px) {
  .header {
    padding: 30px 40px; } }

@media (max-width: 767px) {
  .header {
    flex-direction: column;
    text-align: center;
    padding: 0; }
  .header-nav {
    width: 100%;
    flex-wrap: wrap;
    justify-content: center; }
  .header-nav .header-nav-li {
    width: 50%;
    padding: 8px 0; }
  .header .header-logo {
    width: 48px;
    height: 29px;
    margin: 10px auto 10px auto; } }

.footer {
  background-color: #AA0601;
  padding-top: 32px;
  padding-bottom: 39px; }
  .footer .footer-content {
    margin: auto;
    max-width: 1140px;
    display: flex;
    justify-content: space-between;
    align-items: center; }
    .footer .footer-content .footer-nav {
      display: flex;
      margin-bottom: 40px; }
    .footer .footer-content .footer-nav-li {
      padding-right: 50px; }
    .footer .footer-content .footer-nav a {
      color: #ffffff;
      text-decoration: none; }
    .footer .footer-content .footer-nav a:hover {
      opacity: 0.8; }
    .footer .footer-content .footer-link {
      display: flex; }
    .footer .footer-content .footer-linkimg {
      padding-left: 50px; }
    .footer .footer-content .footer-linkimg a:hover {
      opacity: 0.8; }
  .footer .footer-contact {
    max-width: 1140px;
    margin: auto;
    padding-bottom: 24px;
    border-bottom: 1px solid #ffffff; }
  .footer .footer-contact a {
    font-size: 36px;
    line-height: 1.5;
    text-decoration: none;
    color: #ffffff;
    text-align: center; }
  .footer .footer-contact ul li img {
    margin-right: 20px;
    width: 36px; }
  .footer .footer-data {
    max-width: 1140px;
    margin: auto;
    color: #ffffff;
    font-size: 12px;
    margin-top: 24px;
    display: flex;
    justify-content: space-between; }
  .footer .footer-data a {
    color: #ffffff;
    text-decoration: none;
    font-size: 12px; }
  .footer .footer-data ul {
    display: flex; }
    .footer .footer-data ul li {
      padding-left: 48px; }
    .footer .footer-data ul li a:hover {
      opacity: 0.8; }

@media (max-width: 767px) {
  .footer .footer-content .footer-nav {
    display: none; }
  .footer .footer-data {
    flex-direction: column; }
  .footer-data .Copyright {
    font-size: 16px;
    line-height: 1.5; } }

.faq {
